@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.text-wrapper {
  padding: var(--padding);
  min-width: 400px;
  width: 50%;
  /* background-color: yellow; */
}

.text-header {
  margin-top: calc(var(--margin-large));
}

.img-wrapper {
  width: 25vw;
}

img {
  width: 100%;
  border-radius: 200px;
  padding: var(--padding);
}

@media (max-width: 600px) {
}


.navbar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: var(--side-padding);
  -webkit-align-items: center;
          align-items: center;
  /* background-color: grey; */
  height: 90px;
}

.logo-wrapper {
  width: 250px;
  margin: var(--margin-medium) 0 0 var(--margin-medium);
  /* background-color: yellow; */
}

.logo {
  width: 100%;
}

.tabs-wrapper {
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
  margin: var(--margin-small) var(--margin-small) 0 0;
}

.active-tab {
  background-color: var(--primary-yellow);
  border-radius: 30px 30px 0 0;
  /* box-shadow: 12px 12px 2px 1px rgb(var(--primary-yellow)); */
}

@media (max-width: 600px) {
  .logo-wrapper {
    display: none;
  }

  .navbar {
    padding: 0;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .tabs-wrapper {
    margin: 0;
  }
}

.tab {
  text-decoration: none;
  padding: var(--padding);
  cursor: pointer;
  /* font-size: var(--font-size); */
}

.tab:hover {
  background: var(--primary-yellow__hover);
  border-radius: 30px 30px 0 0;
}

.btn {
  border: 1px solid var(--primary-yellow);
  cursor: pointer;
  padding: var(--padding);
  background-color: var(--primary-yellow);
  color: white;
  font-weight: bold;
}

.btn:hover {
  background: var(--primary-yellow__hover);
}

.btn-wrapper {
  margin-top: var(--margin-medium);
}

:root {
  --primary-yellow: rgb(240, 183, 15);
  --primary-yellow__hover: rgba(240, 183, 15, 0.4);
  --font-size: 1.03em;
  --font-size__responsive: 0.8em;
  --title-medium: 1.5em;
  --title-large: 2em;
  --margin-small: 1em;
  --margin-medium: 2em;
  --margin-large: 3em;
  --padding: 1em;
  --side-padding: 1rem calc((100vw - 1700px) / 2);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Noto Sans JP, sans-serif;
  color: black;
  font-size: 1.03em;
  font-size: var(--font-size);
}

.content-wrapper {
  margin: auto;
  margin-top: 10vh;
  padding: 1rem calc((100vw - 1700px) / 2);
  padding: var(--side-padding);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  text-align: center;
  width: 90vw;
  /* background-color: green; */
}

.title-large {
  font-size: 2em;
  font-size: var(--title-large);
  /* margin: 0 var(--margin-small) 0 var(--margin-small); */
  padding: 1em;
  padding: var(--padding);
}

.title-medium {
  font-size: 1.5em;
  font-size: var(--title-medium);
}

@media (max-width: 600px) {
  :root {
    font-size: 0.8em;
    font-size: var(--font-size__responsive);
  }
  .content-wrapper {
    margin-top: 0;
    padding: 0;
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .title-large {
    padding: 0.2em;
  }
}

